import {Location} from '../location/location'
import {Condition} from '../condition/condition'


export class Car {
    public id: number;
    public name : string;
    public make : string;
    public model : string;
    public year : number;
    public mileage : number;
    public price : number;
    public color : string;
    public locationz : Location[];
    public conditionz : Condition[];
    

    constructor() {
        this.id = this.rndNumND(100000000, 1);
        this.locationz = [new Location()];
        this.conditionz = [new Condition()];
        
    }

    rndNumND(hi: number, low: number) {
        return Math.floor((low + Math.floor(Math.random() * (hi - low))));
    }
}