import { Injectable } from '@angular/core';
import {Car} from './car/car'
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs'

@Injectable({
  providedIn: 'root'
})
export class CarService {
  private carzUrl = 'api/carz';
  carz: Car[];
  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(private http: HttpClient) { this.carz = []; }

  getCarz(): Observable<Car[]> {
    return this.http.get<Car[]>(this.carzUrl)
  }

  getCar(id: number): Observable<Car> {
    return this.http.get<Car>(this.carzUrl+'/'+id);
  }

  updateCar(car: Car): Observable<any> {
    return this.http.put(this.carzUrl, car, this.httpOptions);
  }

  deleteCar(car: Car): Observable<Car> {
    const id =  car.id;
    return this.http.delete<Car>(this.carzUrl+'/'+id, this.httpOptions);
  }
}
