import * as tf from './testfunctions'
import {Car} from '../car/car';

export class CarTestData {
    public car: Car

    constructor() {
        this.car = new Car();
        this.car.id = tf.TestFunctions.randomNumND(1,100000000);
        this.car.name = tf.TestFunctions.randomText(20);
        this.car.make = tf.TestFunctions.randomText(20);
        this.car.model = tf.TestFunctions.randomText(20);
        this.car.year = tf.TestFunctions.randomNumND(1990,2020);
        this.car.mileage = tf.TestFunctions.randomNumND(1,400);
        this.car.price = tf.TestFunctions.randomNum2D(1,400);
        this.car.color = tf.TestFunctions.randomText(10);

    }
}