import * as sample from './sample'
export module TestFunctions {
    export function quartermonth(m: number) {
        return 3*(m-(Math.floor(m/4)*4))+1;
    }

    export function quarteryear(y: number) {
        return 2021-Math.floor(y/4);
    }

    export function randomText(length: number) {
        return sample.SampleTextFunction.getText(length);
    }

    export function randomUpTxt(length: number) {
        return sample.SampleTextFunction.getText(length).toUpperCase();
    }

    export function randomLoTxt(length: number) {
        return sample.SampleTextFunction.getText(length).toLowerCase();
    }

    export function randomTextOld(length: number) {
        var result           = '';
        var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789 ';
        var charactersLength = characters.length;
        for ( var i = 0; i < length; i++ ) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }
        return result;
    }

    export function randomUpTxtOld(length: number) {
        var result           = '';
        var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
        var charactersLength = characters.length;
        for ( var i = 0; i < length; i++ ) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }
        return result;
    }

    export function randomLoTxtOld(length: number) {
        var result           = '';
        var characters       = 'abcdefghijklmnopqrstuvwxyz';
        var charactersLength = characters.length;
        for ( var i = 0; i < length; i++ ) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }
        return result;
    }

    export function randomNum2D(low: number, hi: number) {
        return Math.floor((low + Math.floor(Math.random()*100 * (hi - low))))/100;
    }

    export function randomNumND(low: number, hi: number) {
        return Math.floor((low + Math.floor(Math.random() * (hi - low))));
    }
}