import * as tf from '../testdata/testfunctions'
import {Condition} from '../condition/condition';

export class ConditionTestData {
    public condition: Condition

    constructor() {
        this.condition = new Condition();
        this.condition.id = tf.TestFunctions.randomNumND(1,100000000);
        this.condition.accident = tf.TestFunctions.randomText(3);
        this.condition.rating = tf.TestFunctions.randomText(10);

    }
}