import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';

import { CarService } from '../car.service'
import { Car } from '../car/car'

@Component({
  selector: 'app-carz',
  templateUrl: './carz.component.html',
  styleUrls: ['./carz.component.css']
})
export class CarzComponent implements OnInit {
  dc: Car[];
  carz: Car[];
  selectedCar: Car;

  constructor(  private location: Location,
    private route: ActivatedRoute,
    private carService: CarService,
    private router: Router) {
   }

  ngOnInit() {
    this.getCarz();
  }

  onSelect(car: Car) {
    this.selectedCar = car;
  }

  getCarz(): void {
    this.carService.getCarz().subscribe(carz => this.carz = carz);
  }

  goBack(): void {
    this.location.back();
  }


  add(): void {
    this.router.navigate(['/car/-1']);
  }

  delete(car: Car) {
    this.carz = this.carz.filter(c => c !== car);
    this.carService.deleteCar(car).subscribe();
  }

  setSizeSort() {
    
  }
}
