import * as tf from '../testdata/testfunctions'
import {Location} from '../location/location';

export class LocationTestData {
    public location: Location

    constructor() {
        this.location = new Location();
        this.location.id = tf.TestFunctions.randomNumND(1,100000000);
        this.location.name = tf.TestFunctions.randomText(20);
        this.location.city = tf.TestFunctions.randomText(20);
        this.location.province = tf.TestFunctions.randomText(20);
        this.location.distance = tf.TestFunctions.randomNumND(0,200);

    }
}