import { InMemoryDbService } from 'angular-in-memory-web-api';
import { Car} from './car/car';
import { CarzTestData } from './testdata/carz'
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class InMemoryDataService implements InMemoryDbService {
  createDb() {
    const td = new CarzTestData();
    const carz = td.carz;
    const location = td.locationz;
    const condition = td.conditionz;
    return {location, condition, carz};
  }

  // Overrides the genId method to ensure that a hero always has an id.
  // If the heroes array is empty,
  // the method below returns the initial number (11).
  // if the heroes array is not empty, the method below returns the highest
  // hero id + 1.
  genId(carz: Car[]): number {
    return carz.length > 0 ? Math.max(...carz.map(car => car.id)) + 1 : 1;
  }
}
